.react-datepicker {
  border: none;
  border-radius: 15px;
  overflow: hidden;
  width: 100%;
  border: 2px solid #d0b386;
}

.react-datepicker__header {
  background: #d0b386;
}
.react-datepicker__month {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.react-datepicker__month-container {
  width: 100%;
  height: 100%;
  background: var(--tg-theme-bg-color);
}
.react-datepicker__day-names {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.react-datepicker__week {
  display: flex;
  justify-content: space-between;
}

.react-datepicker__day--selected:hover {
  border-radius: 7px;
  background: #d0b386;
  opacity: 1;
  color: var(--tg-theme-bg-color)
}
.react-datepicker__day-name {
  color: var(--tg-theme-bg-color)
}
.react-datepicker__current-month {
  color: var(--tg-theme-bg-color)
}
.react-datepicker__day {
  transition: 0.3s;
  color: #d0b386;
  border-radius: 7px;
  min-width: 35px;
  min-height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 140%;
}
.react-datepicker__day:hover {
  border-radius: 7px;

}
.react-datepicker__day--keyboard-selected {
  transition: 0.3s;
  opacity: 1;
  background: #d0b386;
  color: var(--tg-theme-bg-color);
}

.react-datepicker__day--today {
  font-weight: 900;
  background: #d0b386;
  opacity: 0.4;
  color: var(--tg-theme-bg-color);
  border-radius: 7px;
}
.react-datepicker__day--today:hover {
  font-weight: 900;
  background: #d0b386;
  opacity: 0.4;
  color: var(--tg-theme-bg-color);
  border-radius: 7px;
}
.react-datepicker__day--selected {
  border-radius: 7px;
  transition: 0.3s;
  opacity: 1;
  background: #d0b386;
  color: var(--tg-theme-bg-color);
}
.react-datepicker__navigation-icon--next {

}
.react-datepicker__navigation-icon--next::before {
  border-color: var(--tg-theme-bg-color);
  border-radius: 3px;
}
.react-datepicker__navigation-icon--previous::before {
  border-color: var(--tg-theme-bg-color);
  border-radius: 3px;
}
.react-datepicker__navigation--previous {
  left: 20%;
}
.react-datepicker__navigation--next {
   right: 20%;
 }
.react-datepicker__navigation--next:hover .react-datepicker__navigation-icon--next::before {
  border-color: var(--tg-theme-bg-color);
  opacity: 0.6;
}
.react-datepicker__navigation--previous:hover .react-datepicker__navigation-icon--previous::before {
  border-color: var(--tg-theme-bg-color);
  opacity: 0.6;
}



















